<template>
  <div class="project" :class="{ complete: project.complete }">
    <div class="actions">
      <h3 @click="toggleDetailsVisibility">{{ project.title }}</h3>
      <div class="icons">
        <router-link :to="{ name: 'EditProject', params: {id :project.id}  }"
          ><span class="material-icons"> edit </span></router-link
        >

        <span @click="deleteProject" class="material-icons"> delete </span>
        <span @click="toggleComplete" class="material-icons tick"> done </span>
      </div>
    </div>
    <div class="details" v-if="detailsVisibility">
      <p>{{ project.details }}</p>
    </div>
  </div>
</template>

<script>
export default {
  props: ["project"],
  data() {
    return {
      detailsVisibility: false,
      uri: "https://my-json-server.typicode.com/CristiNita06/json-server/projects/" + this.project.id,
    };
  },
  methods: {
    toggleDetailsVisibility() {
      this.detailsVisibility = !this.detailsVisibility;
    },
    deleteProject() {
      fetch(this.uri, { method: "DELETE" })
        .then(() => this.$emit("delete", this.project.id))
        .catch((error) => console.log(error));
    },
    toggleComplete() {
      fetch(this.uri, {
        method: "PATCH",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ complete: !this.project.complete }),
      })
        .then(() => {
          this.$emit("complete", this.project.id);
        })
        .catch((error) => console.log(error));
    },
  },
};
</script>

<style>
h3 {
  cursor: pointer;
}

.project {
  margin: 20px auto;
  background: white;
  padding: 10px 20px;
  border-radius: 4px;
  box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.05);
  border-left: 4px solid #e90074;
}

.project.complete {
  border-left: 4px solid rgb(10, 194, 132);
}

.project.complete .tick {
  color: rgb(10, 194, 132);
}

.actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.material-icons {
  font-size: 24px;
  margin-left: 10px;
  color: #bbb;
  cursor: pointer;
}

.material-icons:hover {
  color: #777;
}
</style>
