<template>
  <div class="home">
    <FilterNav
      @filterChange="currentFilter = $event"
      :current="currentFilter"
    />
    <div v-if="projects.length">
      <!-- View all projects -->
      <div v-for="project in filteredProjects" :key="project.id">
        <SingleProject
          :project="project"
          @delete="handleDelete"
          @complete="handleComplete"
        />
      </div>
    </div>
  </div>
</template>

<script>
import SingleProject from "../components/SingleProject.vue";
import FilterNav from "../components/FilterNav.vue";

export default {
  name: "Home",
  components: { SingleProject, FilterNav },
  data() {
    return {
      projects: [],
      currentFilter: "all",
    };
  },
  mounted() {
    fetch("https://my-json-server.typicode.com/CristiNita06/json-server/projects")
      .then((response) => response.json())
      .then((data) => (this.projects = data))
      .catch((error) => console.log(error.message));
  },

  methods: {
    handleDelete(id) {
      this.projects = this.projects.filter((element) => {
        return element.id !== id;
      });
    },
    handleComplete(id) {
      let foundElement = this.projects.find((element) => {
        return element.id == id;
      });
      foundElement.complete = !foundElement.complete;
    },
  },
  computed: {
    filteredProjects() {
      if (this.currentFilter === "completed") {
        return this.projects.filter((element) => element.complete);
      } else if (this.currentFilter === "ongoing") {
        return this.projects.filter((element) => !element.complete);
      } else {
        return this.projects;
      }
    },
  },
};
</script>
